:root {
  --khaki: #e5ddc1;
  --field: #574d43;
  --lake: #0b6683;
  --navy: #0b4e83;
  --base-font: "Montserrat", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

body {
  font-family: var(--base-font);
  color: var(--lake);
  background: var(--khaki);
  height: 100vh;
  max-width: 100vw;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p, button, input {
  color: var(--lake);
  margin: 0;
  font-family: Montserrat, Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

main {
  z-index: 120;
  height: calc(100% - 3rem);
  grid-template-columns: 28rem 1fr;
  gap: 1rem;
  margin: 0;
  padding: 1.5rem;
  display: grid;
  position: relative;
  overflow: hidden;
}

.sidebar {
  border: 4px solid var(--field);
  background: #ffffff80;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  display: flex;
  overflow: auto;
}

#upload-form {
  flex-direction: column;
  flex: 1;
  gap: .75rem;
  display: flex;
}

#upload-form label, #upload-form input[type="file"], #upload-form input[type="file"]::file-selector-button {
  text-transform: uppercase;
  letter-spacing: .32px;
  font-size: .875rem;
  font-weight: bold;
}

#upload-form input[type="file"] {
  border: 2px solid var(--lake);
  background-color: #fff;
}

#upload-form input[type="file"]::file-selector-button {
  color: #fff;
  background-color: var(--lake);
  line-height: 1;
  font-family: var(--base-font);
  border: none;
  outline: none;
  padding: .5rem;
}

button {
  text-align: center;
  color: var(--lake);
  text-transform: uppercase;
  letter-spacing: .32px;
  cursor: pointer;
  border: 2px solid #0000;
  border-color: var(--lake);
  background: #fff;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: .75rem 1rem;
  font-weight: 600;
  line-height: 120%;
  transition: transform .24s;
  display: inline-flex;
  transform: translateY(0);
}

button:hover {
  transform: translateY(-2px);
}

button:disabled {
  opacity: .6;
  cursor: not-allowed;
}

button:disabled:hover {
  transform: none;
}

#preview {
  max-width: 100%;
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.transport-mode__switcher {
  gap: .75rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.transport-mode__switcher li {
  min-width: 4rem;
  min-height: 2rem;
  flex: 1;
  margin: 0;
  padding: 0;
  position: relative;
}

.transport-mode__switcher li input[type="radio"], .transport-mode__switcher li label {
  display: block;
  position: absolute;
  inset: 0;
}

.transport-mode__switcher li input[type="radio"] {
  opacity: .01;
  z-index: 100;
}

.transport-mode__switcher li input[type="radio"] + label {
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .3px;
  border: 2px solid var(--lake);
  color: var(--lake);
  z-index: 120;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  padding: 1rem .75rem;
  font-weight: bold;
  line-height: 120%;
  transition: all .125s ease-in-out;
  display: flex;
}

.transport-mode__switcher li input[type="radio"] + label:hover {
  border-color: var(--navy);
}

.transport-mode__switcher li input[type="radio"] + label:hover, .transport-mode__switcher li input[type="radio"]:checked + label {
  background: var(--lake);
  color: #fff;
}

.form-footer {
  background: #f2ede0;
  gap: .5rem;
  margin: auto -1rem -1rem;
  padding: 1rem;
  display: flex;
  position: sticky;
  bottom: -1rem;
}

.form-footer button {
  background: var(--lake);
  color: #fff;
  border-color: #0000;
  flex: 1;
  font-size: 1rem;
}

#submit-route-btn:disabled {
  background: #a5a5a5;
}

#submit-route-btn {
  color: #fff;
  background-color: var(--lake);
}

#clear-btn {
  color: var(--lake);
  border-color: var(--lake);
  background: #fff;
  flex: 1;
  margin-top: auto;
  padding: 1rem;
}

.preview__card {
  max-width: 100%;
  max-height: 9rem;
  border: 2px solid var(--field);
  background: #fff;
  grid-template-rows: minmax(5px, 7.75rem);
  grid-template-columns: 8rem 1fr;
  align-items: stretch;
  transition: all .24s;
  display: grid;
  position: relative;
  overflow: hidden;
}

.preview__card:hover {
  cursor: pointer;
  transform: translateY(-2px);
  box-shadow: 0 12px 12px -4px #0000001a, 0 12px 8px -1px #0000000f;
}

.preview__card--remove-btn {
  color: #656565;
  opacity: .5;
  box-shadow: none;
  background: none;
  border: none;
  padding: 0;
  font-size: 1rem;
  position: absolute;
  top: .25rem;
  right: .25rem;
}

.preview__card--remove-btn:hover {
  color: #820000;
  border: none;
  transform: translateY(0);
}

.location__card--remove-btn {
  color: #656565;
  opacity: .5;
  box-shadow: none;
  background: none;
  border: none;
  padding: 0;
  font-size: 1rem;
  position: absolute;
  top: .25rem;
  right: .25rem;
}

.location__card--remove-btn:hover {
  color: #820000;
  border: none;
  transform: translateY(0);
}

.preview__card--header {
  justify-content: center;
  padding: .5rem;
  display: flex;
  overflow: hidden;
}

.preview__card--text {
  padding: .75rem .5rem;
  font-size: .875rem;
  overflow: hidden;
}

.preview__card--text h4 {
  text-overflow: ellipsis;
  margin: 0;
  line-height: 1.75;
  overflow: hidden;
}

.preview__card--route {
  border-left: 8px solid var(--lake);
  box-shadow: none;
  border-radius: 0;
  margin: 0;
}

.preview__card--route:hover {
  cursor: initial;
  box-shadow: none;
  transform: none;
}

.preview__card--text dl {
  grid-template-columns: 4rem 1fr;
  align-items: baseline;
  gap: .5rem .25rem;
  margin: 0;
  padding: 0;
  display: grid;
}

.preview__card--text dt {
  text-transform: uppercase;
  letter-spacing: .25px;
  color: gray;
  margin: 0;
  padding: 0;
  font-size: .75rem;
}

.preview__card--text dd {
  margin: 0;
  padding: 0;
}

.preview__image {
  border: 2px solid var(--field);
  max-width: 100%;
  object-fit: cover;
  object-position: center;
  max-height: 100%;
  flex: 1;
  display: block;
  overflow: hidden;
}

.preview__image:after {
  content: "";
  height: 100%;
  width: 100%;
  display: block;
  position: absolute;
  top: 0;
  box-shadow: inset 3px 3px 10px #000;
}

.sidebar h1 {
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  border-top: 1px solid var(--lake);
  border-bottom: 2px solid var(--lake);
  margin: .5rem 0;
  padding: 0;
  font-size: 2.25rem;
}

.sidebar label {
  font-size: .875rem;
  display: inline;
}

.route-panel {
  border: 2px solid var(--lake);
  background: #fff;
}

.carto {
  border: 4px solid var(--field);
  background: #faf4e5;
  padding: 1rem;
  overflow: hidden;
}

#map {
  height: 100%;
  z-index: 100;
  border: 1px dashed var(--field);
}

.leaflet-popup-content-photo {
  margin: .5rem;
}

.marker-photo {
  max-width: 100%;
  min-width: 8rem;
}

.hidden {
  display: none;
}

